import styled from "styled-components"
import { Card, Button, ButtonLink, Loading, Booking } from "components"
// import avatar from "assets/images/avatar.png"
import Cropper from "react-easy-crop"
import { useMutation, useQuery } from "react-apollo"
import { mutateUploadProfilePicture } from "queries/profile"
import { client, renderError, PLACEHOLDER_OWL_IMAGE, newClient } from "helpers"
// import useError from "helpers/hooks/useError"
import { message } from "antd"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { MUTATE_ADD_CHAT_SESSION, QUERY_LIST_CONVERSATION } from "queries/chat"
import { useState } from "react"
import { useHistory } from "react-router"
import { country } from "App"
import { SERVER_BASE_URL } from "helpers/constants"

